.popup-login {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.body {
  background-color: #4f2ee8;
}

.popup-body.login {
  width: 580px;
  text-align: center;
  margin: auto;
  padding: 80px 15px 50px;
  position: relative;
}

.popup-close {
  cursor: pointer;
  font-size: 28px;
  position: absolute;
  top: 24px;
  right: 24px;
}

.popup-body {
  background-color: #f3f3f3;
  border: 2px solid #111;
  border-radius: 18px;
}

.input {
  width: 500px;
  border: 2px solid #111;
  border-radius: 40px;
  margin: 0 auto 30px;
  padding: 25px;
}

.button-signup {
  width: 500px;
  color: #f3f3f3;
  background-color: #111;
  border-radius: 40px;
  margin: 20px auto 30px;
  padding: 20px;
  font-size: 22px;
}

.svg-icon {
  fill: #111;
  width: 28px;
  height: 28px;
  position: absolute;
  top: 50%;
  right: 30px;
  transform: translateY(-50%);
}

.lebel {
  margin-top: 4px;
  position: relative;
}

::-ms-input-placeholder {
  color: #111;
  font-size: 20px;
  font-weight: 500;
}

::placeholder {
  color: #111;
  font-size: 20px;
  font-weight: 500;
}

.link-signup {
  color: rgba(27, 26, 26, .5);
  font-size: 22px;
  font-weight: 700;
}

