.popup-login{   
 position: fixed;
 top: 50%;
 left: 50%;
 transform: translate(-50%, -50%);
}
.body{
 background-color: #4F2EE8;
}
 .popup-body.login {
 margin: auto;
 width: 580px;
 text-align: center;
 padding: 80px 15px 50px 15px;
 position: relative;
 }
.popup-close {
 position: absolute;
 top: 24px;
 right: 24px;
 font-size: 28px;
 cursor: pointer;
 }
 .popup-body {
 background-color: #F3F3F3;
 border: 2px solid #111111;
 border-radius: 18px;
 }
 .input {
 margin: 0 auto 30px auto;
 width: 500px;
 padding: 25px;
 border: 2px solid #111111;
 border-radius: 40px;
 }
 .button-signup {
 width: 500px;
 padding: 20px;
 margin: 20px auto;
 margin-bottom: 30px;
 border-radius: 40px;
 background-color: #111111;
 color: #F3F3F3;
 font-size: 22px;
 }
 .svg-icon {
 position: absolute;
 top: 50%;
 right: 30px;
 transform: translateY(-50%);
 fill: #111111;
 width: 28px;
 height: 28px;
 }
 .lebel {
 position: relative;
 margin-top: 4px;
 }
 ::placeholder {
 font-size: 20px;
 color: #111111;
 font-weight: 500;
 }
 .link-signup {
 color: rgba(27, 26, 26, 0.5);
 font-size: 22px;
 font-weight: 700;
 }